.page {
  background-color: var(--color-bg);
  width: 100%;
  height: 100%;
  max-width: 600px;
  padding: 2rem;
  box-sizing: border-box;
  margin: 0 auto;
}

.page .step {
  padding: 5rem 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 2rem;
}

.page .step img {
  width: 100%;
}

.page .step h2 {
  color: var(--color-dark);
  text-align: center;
}

.page .step-1 .gender-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.page .step-1 .gender-container li {
  margin: 0 0.8rem;
  cursor: pointer;
  background-color: var(--color-light);
  border-radius: 8px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.page .step-1 .gender-container li button {
  width: 100%;
  height: 100%;
  padding: 2rem;
}

.page .step-1 .gender-container li button .icon-gender-mini {
  font-size: 1rem;
  position: absolute;
  top: 10px;
  right: 10px;
  color: var(--color-grey);
}

.page .step-1 .gender-container li button .icon-gender {
  font-size: 4rem;
  color: var(--color-grey);
}

.page .step-1 .gender-container .gender-selected {
  border: 2px solid var(--color-grey);
}

